import React, { Component } from "react";
import AutomationTask from "./AutomationTasks/AutomationTask";
import CreateButton from "../controlls/CreateButton";
import FloatingWindow from "../controlls/FloatingWindow";
//import DraggableComp from "../controlls/DraggableComp";
import { disputeAutomationTask,generateExpectedFields } from "../../actions/backlogActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import ScrollToBottom from 'react-scroll-to-bottom';
import '../AutomationBoard/css/grid_backlog.css';

//import DraggableComp from 'react-draggable'; // The default
//import {DraggableCore} from 'react-draggable'; // <DraggableCore>
//import Draggable, {DraggableCore} from 'react-draggable'; // Both at the same time
 

//import Square from "../../components/controlls/Square";

// import "react-bootstrap/dist/react-bootstrap.min.js";
// import "bootstrap/dist/css/bootstrap.min.css";
// import { Grid, Row, Col } from "react-bootstrap";
import {
  // camelCase,
  // capitalCase,
  // constantCase,
  // dotCase,
  // headerCase,
  // noCase,
  // paramCase,
  pascalCase,
  // pathCase,
  // sentenceCase,
  // snakeCase
} from "change-case";


class AutomationGridBacklog extends Component {
 
  onGenerateExpectedClick(operation_name, tokenTag) {
      // expectedObject[`#ExpectedToken#.${tokenTag}`] = `${fieldName}.${valueResponse}`;
      const expectedObject = {};
      // This It Accepts Multiple Values in the same Line
      // Special Case of its Algorithm
      expectedObject[`#ExpectedToken#.${tokenTag}`] = `code.999,status.Gathering Responses,message.Change IT`;
      
      this.props.generateExpectedFields(operation_name, expectedObject);
    }

    onDisputeClick(backlog_id, pt_id, operation_name, tokenTag, fieldName, valueResponse) {
      this.props.disputeAutomationTask(backlog_id, pt_id, operation_name, tokenTag, fieldName, valueResponse);
    }


    getParsedDate(date){
      date = String(date).split(' ');
      return String(date[0])
      // var days = String(date[0]).split('-');
      // var hours = String(date[1]).split(':');
      // //console.log([parseInt(days[0]), parseInt(days[1])-1, parseInt(days[2]), parseInt(hours[0]), parseInt(hours[1]), parseInt(hours[2])])
      // return [parseInt(days[0]),"-", parseInt(days[1]),"-",parseInt(days[2]), parseInt(hours[0]), parseInt(hours[1]), parseInt(hours[2])];
    }

    getParsedTime(date){
      date = String(date).split(' ');
      return String(date[1]);
      //console.log([parseInt(days[0]), parseInt(days[1])-1, parseInt(days[2]), parseInt(hours[0]), parseInt(hours[1]), parseInt(hours[2])])
      //return [parseInt(hours[0]),":", parseInt(hours[1]),,":", parseInt(hours[2])];
    }


  render() {
    const { automation_tasks_prop } = this.props;
    
    const tasks = automation_tasks_prop.map(automation_task => (
      <AutomationTask key={automation_task.id} automation_task={automation_task} />
    ));

    //var date = new Date("2016-01-04 10:34:23");
    //var formattedDate = format(date, "MMMM Do, yyyy H:mma");
    //console.log(formattedDate);

    let todoItems = [];
    let inProgressItems = [];
    let doneItems = [];
    let allTestCases = [];
    

    for (let i = 0; i < tasks.length; i++) {
      allTestCases.push(automation_tasks_prop[i]);
      if (tasks[i].props.automation_task.status === "IN_HOLD") {
        todoItems.push(tasks[i]);
      }

      if (tasks[i].props.automation_task.status === "FAILLED") {
        inProgressItems.push(tasks[i]);
      }

      if (tasks[i].props.automation_task.status === "SUCCESS") {
        doneItems.push(tasks[i]);
      }
    }

    if (allTestCases){
      allTestCases = allTestCases.sort((a,b) => a.orderTest - b.orderTest);
    }
    
    let RenderHeaderGrid;
    let RenderDataGrid
    // let RenderColGrp


    // const renderColGrp = (renderHeaderGrid) => {
    //   //let header = Object.keys(this.state.students[0])
    //   var firstElem = true;
    //   return renderHeaderGrid.map((key, index) => {
    //     if (firstElem) {
    //       firstElem = false;
    //       return <col/>;
    //     }else{
    //       return <col/>;
    //     }
    //   })
    // }

    const renderTableHeader = (firstAutoTask) => {
      //let header = Object.keys(this.state.students[0])
      //let header = Object.keys(firstAutoTask[0])
      let header = [];
      
      //header.push("bodyResponse");
      header.push("testCaseName");
      header.push("projectSequence");
      header.push("tokenTag");
      header.push("fieldName");
      header.push("expectedValue");
      header.push("valueResponse");
      header.push("succeeded");
      header.push("priority");
      header.push("dueDate");
      header.push("update_At");
      header.push("In Hold")
      header.push("Failled")
      header.push("Success")
      header.push("Reset-Expected")
      header.push("orderTest");
      header.push("Dispute")
      
      return header.map((key, index) => {
          if (key !== "id") {  
            if (key === "testCaseName"){
              return <th key={index} className="test-case-name">
                      {pascalCase(key)}
                     </th>
              {/* <div className="container" style = {{ marginLeft: 15 }}> */}
            }else{
              return <th key={index}>{pascalCase(key)}</th>
            }
          }else{
           return null;    
          }
        }
      )
    }

    
    const statusPriority = (priority) => {
      //PROJECT IDENTIFIER CHECK
      if (priority === 1) {
        return (
          // <div className="alert alert-danger text-center" role="alert">
          <div className="border border-high text-light">HIGH</div>
        );
      } else if (priority === 2) {
        return (
          // <div className="alert alert-danger text-center" role="alert">
          <div className="border border-medium text-light">MEDIUM</div>
        );
      } if (priority === 3) {
        return (
          // <div className="alert alert-info text-center" role="alert">
          <div className="border border-low text-light">LOW</div>
        );
      }
  }

   const statusEmpty = (status) => {
      return (
      // <div className="alert alert-danger text-center" role="alert">
      <CreateButton
          link="#"
          caption="      X      "
          cssClass="btn btn-light"
        />
      );
   }

   const spanStatusEmpty = (status) => {
    return (
    // <div className="alert alert-danger text-center" role="alert">
    <div className="border border-empty text-light">XXXXXXXX</div>
    );
 }
  
    const statusAlgorithm = (status) => {
      //PROJECT IDENTIFIER CHECK
      if (status === "IN_HOLD") {
        return (
          // <div className="alert alert-danger text-center" role="alert">
            <CreateButton
                      link="#"
                      caption={status}
                      cssClass="btn btn-in-hold-task text-white ml-4"
                      />
        );
      } else if (status === "FAILLED") {
        return (
          // <div className="alert alert-danger text-center" role="alert">
          <CreateButton
          link="#"
          caption={status}
          cssClass="btn btn-failled-task text-white ml-4"
          />  
        );
      } if (status === "SUCCESS") {
        return (
          // <div className="alert alert-info text-center" role="alert">
          <CreateButton
                link="#"
                caption={status}
                cssClass="btn btn-success-task text-white ml-4"
              />       
        );
      }
     else {
      return (
      // <div className="alert alert-info text-center" role="alert">
      // <div className="alert alert-danger text-center" role="alert">
      <td><CreateButton
          link="#"
          caption="      X      "
          cssClass="btn btn-light"
        />
        </td>
      );
    }
  }
  

 const renderTableData = (allCases) => {
        //return this.state.students.map((student, index) => {
        {/* <div className="container" style = {{ marginLeft: 15 }}> */}
          
    return  allCases.map((testCases, index) => {
		   const {id, projectIdentifier, projectSequence, status, priority, dueDate, testCaseName, bodyResponse, update_At, operationName, tokenTag, fieldName, expectedValue, valueResponse, orderTest, succeeded} = testCases //destructuring
       return (
            <tr key={id} className="other-cols">
            <td className="test-case-name"><div className="fill-td">{testCaseName}</div></td>
            <td>{projectSequence}</td>
            <td className="test-case-name"><div className="fill-td">{tokenTag}</div></td>
            <td>{fieldName}</td>
            <td>{expectedValue}</td>
            <td>{valueResponse}</td>
            <td>{!succeeded && (
                  "NO"
                  )||"Yes"}</td>
            <td className="td-some-cols"><div className="fill-center">{statusPriority(priority)}</div></td>    
            <td>{dueDate}</td>
            <td style = {{ width: 80 }}>{update_At && (<>{this.getParsedDate(update_At)}<br/>{this.getParsedTime(update_At)}</>)}</td>
            <td className="td-some-cols">{status==="IN_HOLD" && (
                  statusAlgorithm(status)
                  )||spanStatusEmpty()}</td>    
            <td className="td-some-cols">{status==="FAILLED" && (
                  statusAlgorithm(status)
                  )||spanStatusEmpty()}</td>          
            <td className="td-some-cols">{status==="SUCCESS" && (
                  statusAlgorithm(status)
                  )||spanStatusEmpty()}</td>
            <td>          
            <button
            className="btn btn-warning ml-4"
            onClick={this.onGenerateExpectedClick.bind(
              this,
              operationName, 
              tokenTag
            )}
          >
            Reset
          </button></td> 
          <td>{orderTest}</td>
         <td>          
            <button
            className="btn btn-info ml-4"
            onClick={this.onDisputeClick.bind(
              this,
              projectIdentifier,
              projectSequence,
              operationName, 
              tokenTag,
              fieldName,
              valueResponse
            )}
          >
            Dispute
          </button></td>           
            </tr>
            
          )
        })
    }
  

    RenderHeaderGrid = renderTableHeader(allTestCases);
    RenderDataGrid = renderTableData(allTestCases);
    //RenderColGrp = renderColGrp(RenderHeaderGrid);


    return (
         <>
         <FloatingWindow></FloatingWindow>
           <ScrollToBottom>
             <table id='automation' className="fixed">
               {/* <colgroup>
                  {RenderColGrp}             
               </colgroup> */}
             <thead>
             <tr>{RenderHeaderGrid}</tr>
             </thead>  
             <tbody>
                 {RenderDataGrid}
             </tbody>
             <tfoot>
               <tr>{RenderHeaderGrid}</tr>
             </tfoot>
             </table>
          </ScrollToBottom>   
         </>
         
     );
  }
}

AutomationGridBacklog.propTypes = {
  disputeAutomationTask: PropTypes.func.isRequired,
  generateExpectedFields: PropTypes.func.isRequired
};
export default connect(
  null,
  { disputeAutomationTask, generateExpectedFields }
)(AutomationGridBacklog);

