import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { logout } from "../../actions/securityActions";

class Header extends Component {
  logout() {
    this.props.logout();
    window.location.href = "/";
  }
  render() {
    const { validToken, user, admin } = this.props.security;
    //const { profileIdentifier } = user;
    const userIsAuthenticatedAdmin = admin => {
      if (admin) {
        return (
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <Link className="nav-link" to="/profiles">
                Profiles CV
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/dashboard">
                Dashboard
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/automation">
                Data flow
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/joinChat">
                Join To Chat
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/timer">
                Timer
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/pomodoro">
                Pomodoro
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/draggable">
                Draggable
              </Link>
            </li>
          </ul>
        );
      } else {
        return null;
      }
    };

    const userIsAuthenticated = (
      <div className="collapse navbar-collapse" id="mobile-nav">
        {userIsAuthenticatedAdmin(admin)}
        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
          {/* <Link className="nav-link" to={`/editcv/${profileIdentifier}`}> */}
          <Link className="nav-link" to="/dashboard">
 			       <i className="fas fa-user-circle mr-1" />
              {user.fullName}
            </Link>
          </li>
          <li className="nav-item">
            <Link
              className="nav-link"
              to="/logout"
              onClick={this.logout.bind(this)}
            >
              Logout
            </Link>
          </li>
        </ul>
      </div>
    );

    const userIsNotAuthenticated = (
      <div className="collapse navbar-collapse" id="mobile-nav">
        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/register">
              Sign Up
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/login">
              Login
            </Link>
          </li>
        </ul>
      </div>
    );

    let headerLinks;

    if (validToken && user) {
      headerLinks = userIsAuthenticated;
    } else {
      headerLinks = userIsNotAuthenticated;
    }

    return (
      <nav className="navbar navbar-expand-sm navbar-dark bg-primary mb-4">
        <div className="container">
          <Link className="navbar-brand" to="/">
            Automation Test Management Tool
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#mobile-nav"
          >
            <span className="navbar-toggler-icon" />
          </button>
          {headerLinks}
        </div>
      </nav>
    );
  }
}

Header.propTypes = {
  logout: PropTypes.func.isRequired,
  security: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  security: state.security
});

export default connect(
  mapStateToProps,
  { logout }
)(Header);
